	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	export default {
		components: {
			uniLoadMore	
		},
		data() {
			return {
				cateMaskState: 0, //分类面板展开状态
				headerPosition:"fixed",
				headerTop:"0px",
				loadingType: 'more', //加载更多状态 
				cateList: [],
				goodsList: [],
				listQuery: { //查询分页
					name:"",
					cateId:0,//已选三级分类id
					filterIndex: 0,
					priceOrder: 0, //1 价格从低到高 2价格从高到低
					pageNo: 1,
					pageSize: 10
				},
			};
		},
		
		onLoad(options){



 			this.listQuery.cateId=options.cateId;
 			this.loadCateList(options.fid);
			this.loadData();
		},
		onPageScroll(e){
			//兼容iOS端下拉时顶部漂移
			if(e.scrollTop>=0){
				this.headerPosition = "fixed";
			}else{
				this.headerPosition = "absolute";
			}
		},
		//下拉刷新
		onPullDownRefresh(){
			this.listQuery.pageNo=1;
			this.loadData('refresh');
		},
		//加载更多
		onReachBottom(){
			this.listQuery.pageNo++;
			this.loadData();
		},
		methods: {
			imgPath:function(path){
				if(path==''||path==null||path=='null'){
					return "/static/errorImage.jpg"				 
				}else if (path!=''&&path.indexOf('http')>=0) {
					return path;				 
				}
				return this.siteHost + path;
			},
			//加载分类
			async loadCateList(fid){
				var _this = this;
				//获取数据
				this.sendRequest({
					url : "get_product_cat",
					data : {pid : fid},
					method : "get",
					header: {'content-type' : "application/x-www-form-urlencoded"},
					success:function (res) {
						let cateList= res.data;  
						_this.cateList=cateList;
					}
				});  
			},
			//加载商品 ，带下拉刷新和上滑加载
			async loadData(type='add', loading) {
				//没有更多直接返回
				if(type === 'add'){
					if(this.loadingType === 'nomore'){
						return;
					}
					this.loadingType = 'loading';
				}else{
					this.loadingType = 'more'
				}
				//加载第一页
				if(type === 'refresh'){
					if(loading == 1){
						this.listQuery.pageNo=1;
					}
				}
				let goodsPage={};
				let _this=this;
				this.sendRequest({
					url : "get_product_list",
					data : this.listQuery,
					method : "get",
					header: {'content-type' : "application/x-www-form-urlencoded"},
					success:function (res) {
						goodsPage= res.data;  
						let goodsList = goodsPage.datas;
						if(type === 'refresh'){
							_this.goodsList = [];
						}
						_this.goodsList = _this.goodsList.concat(goodsList); 
 						//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
						_this.loadingType  = goodsPage.pageNo<=goodsPage.totalPage ? 'nomore' : 'more';
						if(type === 'refresh'){
							if(loading == 1){
								uni.hideLoading()
							}else{
								uni.stopPullDownRefresh();
							}
						}
 					}
				});
				
				
				//筛选，测试数据直接前端筛选了
				// if(this.filterIndex === 1){
				// 	goodsList.sort((a,b)=>b.sales - a.sales)
				// }
				// if(this.filterIndex === 2){
				// 	goodsList.sort((a,b)=>{
				// 		if(this.priceOrder == 1){
				// 			return a.price - b.price;
				// 		}
				// 		return b.price - a.price;
				// 	})
				// }
				
				
			},
			//筛选点击
			tabClick(index){
				if(this.listQuery.filterIndex === index && index !== 2){
					return;
				}
				this.listQuery.filterIndex = index;
				if(index === 2){
					this.listQuery.priceOrder = this.listQuery.priceOrder === 1 ? 2: 1;
				}else{
					this.listQuery.priceOrder = 0;
				}
				uni.pageScrollTo({
					duration: 300,
					scrollTop: 0
				})
				this.loadData('refresh', 1);
				uni.showLoading({
					title: '正在加载'
				})
			},
			//显示分类面板
			toggleCateMask(type){
				let timer = type === 'show' ? 10 : 300;
				let	state = type === 'show' ? 1 : 0;
				this.cateMaskState = 2;
				setTimeout(()=>{
					this.cateMaskState = state;
				}, timer)
			},
			//分类点击
			changeCate(item){
				this.listQuery.cateId = item.id;
				this.toggleCateMask();
				uni.pageScrollTo({
					duration: 300,
					scrollTop: 0
				})
				this.loadData('refresh', 1);
				uni.showLoading({
					title: '正在加载'
				})
			},
			//详情
			navToDetailPage(item){
				//测试数据没有写id，用title代替
				let id = item.id;
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
			stopPrevent(){}
		},
	}
